<template>
  <div id="temp-comp" v-if="set == 2">
    <h4
      class="clickable"
      data-toggle="collapse"
      :data-target="'#' + getName(team.name)"
      v-once
    >{{team.name}}</h4>
    <div
      id="teamList"
      class="clickable d-flex justify-content-start"
      data-toggle="collapse"
      :data-target="'#' + getName(team.name)"
    >
      <!-- <div v-for="c in team.characters" :key="uuid(c)">{{c}}</div> -->
      <ChampIcon
        v-once
        class="mx-2 clickable"
        v-for="c in team.characters"
        :id="'icon-' + getName(c.name)"
        :champ="{name: c.name, set: [set], items: c.items}"
        :showItems="true"
        :key="uuid(c)"
      />
    </div>
    <div :id="getName(team.name)" class="collapse row">
      <div class="yard row mt-3 mb-3 col-4" style="height: 175px;">
        <ul id="hexGrid" style="width: 100%; margin-left: 15px;">
          <ChampHex
            v-once
            :champs="team.characters"
            :index="p"
            :teamSet="team.set[0]"
            :key="uuid(p)"
            :droppable="false"
            v-for="p in 28"
          />
        </ul>
      </div>
      <div class="col-8"></div>
    </div>
  </div>
  <div v-else-if="set != 1">
    <div class="yard row mt-3 mb-3" style="height: 175px;">
      <ul id="hexGrid" style="width: 100%; margin-left: 15px;">
        <ChampHex :droppable="true" :key="uuid(p)" :index="p" v-for="p in 28" />
      </ul>
    </div>
  </div>
</template>

<script>
import ChampHex from "./ChampHex";
import ChampIcon from "./ChampIcon";

export default {
  name: "TeamComp",
  data() {
    return {
      isActive: false
    };
  },
  components: { ChampHex, ChampIcon },
  props: {
    team: Object,
    set: Number
  }
};
</script>

<style>
.clickable {
  cursor: pointer;
}
.yard {
  width: 385px !important;
  margin: auto;
  padding: auto !important;
}

#hexGrid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: 0 auto;
  overflow: hidden;
  font-family: Raleway, sans-serif;
  font-size: 15px;
  list-style-type: none;
  padding-bottom: 3%;
}

#hexGrid .hex:nth-child(14n + 1) {
  margin-left: 6%;
}
</style>