<template>
  <div
    style="width: 50px;"
    data-toggle="tooltip"
    data-html="true"
    data-placement="auto"
    class="tooltipselector"
    :data-original-title="info"
  >
    <img
      class="icon border"
      :src="getAvatarURL(champ.name, 2)"
      :alt="champ.name"
      :title="champ.name"
      :data-name="champ.name"
      :data-set="2"
      :draggable="draggable"
      @dragstart="drag($event)"
      :style="{borderColor: getTierColor(champ.cost), borderImage: getTierColor(champ.cost), borderImageSlice: imageSlice}"
    />
    <!-- <div
          class="demo m-1"
          v-for="c in listChamp.filter(c => c.set[0] == 2)"
          :key="c.id"
          :data-name="c.name"
          :data-set="c.set[0]"
          v-on:dragstart="drag($event)"
          :draggable="true"
    ></div>-->
    <div class="items d-flex justify-content-center my-1" v-if="showItems">
      <img
        class="item"
        :src="getItemURL(item)"
        :alt="item"
        :title="item"
        v-for="item in champ.items"
        :key="uuid(item)"
      />
    </div>
    <div class="d-none">
      <ChampInfo :id="'info-' + getName(champ.name)" :champ="champObject[champ.name]" />
    </div>
  </div>
</template>

<script>
import ChampInfo from "./ChampInfo";

export default {
  name: "ChampIcon",
  components: { ChampInfo },
  props: {
    champ: Object,
    draggable: {
      type: Boolean,
      default: false
    },
    showItems: {
      type: Boolean,
      default: false
    },
    showInfo: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      info: "",
      imageSlice: "1 !important"
    };
  },
  methods: {
    drag: function(ev) {
      ev.dataTransfer.setData("name", ev.target.dataset.name);
      ev.dataTransfer.setData("set", ev.target.dataset.set);
    },
  },
  mounted() {
    if (document.querySelector("#info-" + this.getName(this.champ.name))) {
      this.info = document.querySelector(
        "#info-" + this.getName(this.champ.name)
      ).outerHTML;
    }

    let buttonsWithOpenTooltips = [],
      mousedOverTooltip, isTooltipClickablePreference = true;

    function hideOpenTooltips() {
      for (let button of buttonsWithOpenTooltips) {
        $(button).tooltip("hide");
      }
      buttonsWithOpenTooltips = [];
    }
    $("#icon-" + this.getName(this.champ.name) + '[data-toggle="tooltip"]')
      .tooltip({ trigger: 'manual', boundary: 'window' })
      .on("mouseenter touchend focus", function() {
        // Hide all other open tooltips
        buttonsWithOpenTooltips.push(this);
        
        $(this).tooltip("show");
        
        $(".tooltip")
          .on("mouseenter focus", function() {
            // Track last button moused over
            mousedOverTooltip = this;
          })
          .on("mouseleave", function() {
            // Hide this tooltip when user mouses out of it
            mousedOverTooltip = null;
            hideOpenTooltips();
          })
          .on("click", function() {
            // Hide this tooltip when user clicks it
            hideOpenTooltips();
          });
      })
      .on("mouseleave", function() {
        // .setTimeout needed because tooltip mouseenter fires after button mouseleave
        let _this = this;
        // isTooltipClickablePreference = uiUtils.getPreference('tooltip-clickable', false);
        if (isTooltipClickablePreference) {
          window.setTimeout(function() {
            if (
              $(".tooltip").length > 0 &&
              !$(".tooltip").is($(mousedOverTooltip))
            ) {
              $(_this).tooltip("hide");
            }
          }, 100);
        } else {
          $(_this).tooltip("hide");
        }
      });
  }
};
</script>

<style>
.demo {
  height: 50px;
  width: 50px;
  display: inline-block;
  background: black;
}
.icon {
  width: 50px;
  height: auto;
}
.item {
  width: 20px;
  height: auto;
}
</style>