<template>
  <div class>
    <div :id="'basic-info-' + getName(champ.name)" class="row">
      <div class="col-3">
        <img :src="getAvatarURL(champ.name, 2)" :alt="champ.name" width="50px" />
        <div>{{champ.name}}</div>
      </div>
      <div class="col-6 text-center">
        <div>
          <img class="origin-type" :src="getIconURL(champ.origin[0])" :alt="champ.origin[0]" />
          {{champ.origin[0]}}
        </div>
        <div v-for="t in champ.type" :key="uuid(t)">
          <img class="origin-type" :src="getIconURL(t)" :alt="t" />
          {{t}}
        </div>
      </div>
      <div class="col-3">
        <div>
          <img src="https://rerollcdn.com/ui/icon-gold.svg" width="10px" height="10px" alt />
          {{ champ.cost }}
        </div>
        <!-- <i
          class="fas fa-chevron-right btn btn-dark"
          data-toggle="tab"
          href="#skill-info"
          @click="alert()"
        ></i> -->
      </div>
    </div>
    <div :id="'skill-info-' + getName(champ.name)" class="row">
      <div class="col-3">
        <img width="40px" :src="getSkillURL(champ.name, champ.skill[0].name)" :alt="champ.skill[0].name">
        <div>{{ champ.skill[0].name }} ({{ champ.skill[0].type}})</div>
      </div>
      <div class="col-9 text-left">
        <div>{{ champ.skill[0].tags[0].name}}: {{ champ.skill[0].tags[0].bonus.toString().replace(/,/g, '/') }}</div>
        <div>{{ champ.skill[0].description }}</div>
        <!-- <i class="fas fa-chevron-left btn btn-dark" data-toggle="tab" href="#basic-info"></i>
        <i class="fas fa-chevron-right btn btn-dark" data-toggle="tab" href="#stat-info"></i> -->
      </div>
    </div>
    <div :id="'stat-info-' + getName(champ.name)" class="row text-left">
      <!-- <div class="col-auto st"> -->
      <div class="col-6 px-4 py-0">Health: {{ champ.health.toString().replace(/,/g, '/') }}</div>
      <div class="col-6 px-4 py-0">Mana: {{ champ.mana_cost }}</div>
      <div class="col-6 px-4 py-0">ATK: {{ champ.attack[0] }}</div>
      <div class="col-6 px-4 py-0">Atk.spd: {{ champ.speed }}</div>
      <div class="col-6 px-4 py-0">Armor: {{ champ.armor[0] }}</div>
      <div class="col-6 px-4 py-0">Resistance: {{ champ.resistance[0] }}</div>
      <div class="col-6 px-4 py-0">Range: {{ champ.range }}</div>
      <!-- </div> -->
      <!-- <div class="col-auto">
        <i class="fas fa-chevron-left btn btn-dark" data-toggle="tab" href="#skill-info"></i>
      </div>-->
    </div>
  </div>
</template>

<script>
export default {
  name: "ChampInfo",
  props: {
    champ: Object
  },
  data() {
    return {
      page: 1
    };
  },
  methods: {},
  mounted() {
    window.changePage = function(ev, name) {
      // $('a[href="#"]').tab('show')
      console.log(ev, name);
    };
  }
};
</script>

<style>
.origin-type {
  width: 20px;
  height: 20px;
}
</style>